import React from 'react'

import CentrmedLogo from '../../icons/CentrmedLogoSmall.svg';

import './doctor.css';
import { Button } from '../Button/Button';
import { GetAvatar } from '../GetAvatar/GetAvatar';
import { useModal } from 'react-modal-hook';
import ReactModal from 'react-modal';
import Reservation from '../Modals/Reservation/Reservation';

function Doctor({ name, specialty, country, id, region, city, price }) {
  const [showDataPicker, hideDataPicker] = useModal(() => (
    <ReactModal
      isOpen
      className="App__modal-content"
      ariaHideApp={false}
    >
      <div onClick={hideDataPicker}>
        <Reservation hideDataPicker={hideDataPicker} />
      </div>

    </ReactModal>
  ));

  return (
    <div className="doctor">
      <GetAvatar id={id} />
      <span className="doctor__name">{name}</span>
      <span className="doctor__specialty">{specialty}</span>
      <span className="doctor__residence">{city && city + ', '}{region && region + ', '}{country}</span>
      {price && <span className="doctor__price">Ціна консультації: {price}грн</span>}
      <div className="doctor__buttonWrapper">
        <Button onClick={showDataPicker}>Записатися</Button>
      </div>
      <img className='doctor_logo' src={CentrmedLogo} alt="Centrmed Logo" />
    </div>
  )
}

export default Doctor