import React, { useEffect, useState } from 'react'
import axios from 'axios';
import Doctor from '../Doctor/Doctor';
import $api, { API_URL } from '../../http';

import './doctors.css';
import { routeUser } from '../../http/routeBack';

function Doctors() {
  useEffect(() => {
    $api.get(routeUser.serchDoctor)
      .then(function (response) {

        setDoctors(response.data.doctors);
      }).catch(function (error) {
        console.error(error);
      });
  }, [])

  const [doctors, setDoctors] = useState([]);

  return (
    <div className='doctors'>
      {doctors && doctors.map(doctor => {
        return (<Doctor id={doctor.id}
          key={doctor.id}
          city={doctor.city}
          country={doctor.country}
          region={doctor.region}
          name={doctor.name}
          specialty={doctor?.mainSpecialist ? doctor?.mainSpecialist : 'Немає спеціальності'}
          residence={`${doctor?.doctorInfo?.Residence?.country}`} />
        )
      }
      )}
    </div>
  )
}

export default Doctors