import { Suspense, lazy } from 'react'
import { Loader } from '../../Loader/Loader'
import { ReactComponent as ButtonClose } from '../../../icons/closeButton.svg';
import './Reservation.css'

const DateTimePicker = lazy(() => import('../../DateTimePicker/DateTimePicker'))

function Reservation({ hideDataPicker }) {


	return (<section className='Reservation'
		onClick={(e) => e.stopPropagation()}
	>
		<Suspense fallback={<Loader />}>
			<DateTimePicker />
		</Suspense>
		<ButtonClose className='Reservation__close' onClick={() => hideDataPicker()} />
	</section>)
}


export default Reservation